<template>
  <b-list-group>
    <b-list-group-item 
      v-for="(special, index) in item.specials" 
      :key="index" 
      @click="select(special, item)" 
      :variant="special.is_selected ? 'info':'light'">
    <span v-show="special.is_selected">[客製]</span>
    {{`${special.name}, $ ${special.price}`}}
    </b-list-group-item>
  </b-list-group>
</template>

<script>
export default {
  name: 'CustomList',
  data () {
    return {
    }
  },
  props: {
    item: Object,
  },
  methods: {
    customAddTo: function (item) {
      let reducer = item.specials.reduce(function (result, e) {
        if(e.is_selected) {
          let obj = {
            name: e.name,
            price: e.price
          }
          result.arr.push(obj)
          result.num += e.price
        }
        return result
      }, {
        arr: [],
        num: 0
      })

      item.specials_selected = reducer.arr
      item.specials_price_total = reducer.num

      this.calculatePriceTotal(item)
    },
    calculatePriceTotal: function(item) {
      item.price_total = item.specials_price_total + item.price
    },
    select: function(special, item){
      special.is_selected = !special.is_selected
      this.customAddTo(item)
    },
  }
}
</script>



