<template>
  <div>
    <b-table responsive :items="cart" :fields="fields">
      <template slot="name" slot-scope="row">
        <p class="name-tag" v-if="row.item.specials.length > 0">客製</p>
        <p class="name">{{row.item.name}}</p>
      </template>
      <template slot="specials" slot-scope="row">
        <b-list-group-item v-for="(item, index) in row.value" :key="index">
          <p class="name">{{item.name}}<span class="name-tag">{{item.price}}$</span></p>
        </b-list-group-item>
      </template>
      <template slot="number" slot-scope="row">
        <div v-if="nonsense"></div>
        <div v-if="numberVariable" @click="reRenderThis()">
          <b-button 
            v-if="row.field.input_mode === false && row.field.button_mode === false" 
            @click="row.field.button_mode = true" 
            variant="outline-danger">
            <span class="number">{{ row.value }}</span>
          </b-button>
          <div v-if="row.field.button_mode === true">
            <b-button @click="row.field.input_mode = true, row.field.button_mode = false">改量</b-button>
            <b-button @click="deleteItem(row.index), row.field.button_mode = false">刪除</b-button>
          </div>
          <div v-if="row.field.input_mode === true">
            <b-button @click="row.item.number = parseInt(row.item.number) + 1" size="sm">＋</b-button>
            <span class="number">{{row.item.number}}</span>
            <b-button @click="row.item.number = parseInt(row.item.number) - 1" :disabled="row.item.number === 1" size="sm">－</b-button>
            <b-button @click="row.field.input_mode = false" size="sm">確定</b-button>
          </div>
        </div>
        <div v-else >
          <h4>{{ row.value }}</h4>
        </div>
      </template>
      <template slot="price" slot-scope="row">
        <p class="number">{{ row.item.number * row.item.price }}</p>
      </template>
    </b-table>
  </div>
</template>

<script>
export default {
  props: {
    numberVariable: {
      type: Boolean,
      default: true
    },
    cart: {
      type: Array,
    }
  },
  data () {
    return {
      fields: [
        { key: 'name', label: '名稱'},
        { key: 'specials', label: '客製'},
        { key: 'number', label: '數量', input_mode: false, button_mode: false},
        { key: 'price', label: '價格'},
      ],
      nonsense: false,
    }
  },
  computed: {

  },
  methods:{
    deleteItem: function(index) {
      this.cart.splice(index, 1)
    },
    reRenderThis: function(){
      this.nonsense = !this.nonsense
    }
  },
}
</script>
<style lang="stylus" scoped>
p
div
  white-space nowrap
.name
  font-size 1.2em
.name-tag
  color red
  border 1px solid black
.number
  font-size 1.5em
</style>


