<template>
  <div class="container">
    <div class="item-ten" v-if="keyType === 'ten'">
      <button class="btn btn-ten" v-for="(key, index) in ten_btns" :key="index" @click="input(key)">{{ key }}</button>
      <b-button class="btn-zh" variant="outline-danger" @click="input('AC')">AC</b-button>
      <b-button class="btn-zh" variant="outline-success" @click="$emit('confrim')">確認</b-button>
    </div>
    <div class="item-int" v-if="keyType === 'int'">
      <b-button variant="outline-primary" @click="$emit('equal')">剛好</b-button>
      <button class="btn btn-int" v-for="(key, index) in int_btns" :key="index" @click="input(key)">{{ key }}</button>
      <b-button variant="outline-success" @click="$emit('confrim')">確認</b-button>
    </div>
  </div>
</template>
<script>
export default {
  name: 'NumPad',
  data: function () {
    return {
      ten_btns: [1, 2, 3, 4, 5, 6, 7, 8, 9, 0],
      num: 0
    }
  },
  props: {
    keyType: {
      type: String,
      required: false,
      default: ''
    },
    int_btns: {
      type: Array,
      default: () => [100, 500, 1000]
    },
  },
  methods: {
    input: function(input) {
      if(this.ten_btns.includes(input)){
        this.num *= 10
        this.num += input
      } else {
        if(input === 'AC'){
          this.num = 0
        } else {
          this.num = input
        }
      }
      this.$emit('touchBtn', this.num)
    }
  }
}
</script>

<style lang="stylus" scoped>
.container
  display grid || inline-grid
  grid-template-columns 1fr
  grid-row-gap 1em
  .item-ten
  .item-int
    display grid || inline-grid
    grid-template-columns repeat(3, 1fr)
    grid-auto-rows 4em
    grid-gap 1em 1em
  .item-int
    grid-template-columns repeat(2, 1fr)
.btn
  font-size 2em
  color black
  border-radius 0.5em
  text-shadow 0 1px 1px rgba(0, 0, 0, 0.2)
.btn-ac
  color white
  font-size 1em
  background rgb(202, 60, 60)
.btn-ten
  border .1em solid black
  background white
.btn-int
  background light
.btn-zh
  font-size 1.2em
</style>


